import { useState } from "react";
import { Link } from "react-router-dom";
import HeroImg from "../../img/HeroImg";
import "./SubNav.css";
const SubNav = () => {
  const [toggleTab, setToggleTab] = useState(1);
  const tabCLicked = (index) => {
    setToggleTab(index);
  };

  return (
    <>
      <div className="hero-img">
        <div className="hero-img-container">
          <HeroImg />
        </div>
        <div className="hero-img-content">
          <h1>Documentation</h1>
          <h3>
            Amra kaj ta sompuro vabe korte perechi r tai er jonno amar client k
            onek jhamela pohate hosce
          </h3>
        </div>
      </div>
      <div className="Sub-header">
        <div className="Sub-header-container">
          <Link
            exact
            to="/"
            className={toggleTab === 1 ? "Sub-anchor active" : "Sub-anchor"}
            onClick={() => tabCLicked(1)}
          >
            All Products
          </Link>
          <Link
            exact
            to="/creation"
            className={toggleTab === 2 ? "Sub-anchor active" : "Sub-anchor"}
            onClick={() => tabCLicked(2)}
          >
            Creation
          </Link>
          <Link
            exact
            to="/devops"
            className={toggleTab === 3 ? "Sub-anchor active" : "Sub-anchor"}
            onClick={() => tabCLicked(3)}
          >
            DevOps
          </Link>
          <Link
            exact
            to="/backend"
            className={toggleTab === 4 ? "Sub-anchor active" : "Sub-anchor"}
            onClick={() => tabCLicked(4)}
          >
            Backend
          </Link>
          <Link
            exact
            to="/multiplayer"
            className={toggleTab === 5 ? "Sub-anchor active" : "Sub-anchor"}
            onClick={() => tabCLicked(5)}
          >
            Multiplayer
          </Link>
          <Link
            exact
            to="/playerengagement"
            className={toggleTab === 6 ? "Sub-anchor active" : "Sub-anchor"}
            onClick={() => tabCLicked(6)}
          >
            Player Engagement
          </Link>
          <Link
            exact
            to="/growth"
            className={toggleTab === 7 ? "Sub-anchor active" : "Sub-anchor"}
            onClick={() => tabCLicked(7)}
          >
            Growth
          </Link>
          <Link
            exact
            to="/monetization"
            className={toggleTab === 8 ? "Sub-anchor active" : "Sub-anchor"}
            onClick={() => tabCLicked(8)}
          >
            Monetization
          </Link>
        </div>
      </div>
    </>
  );
};
export default SubNav;
