import { useRef, useState } from "react";
import { Link } from "react-router-dom";
import DevOpsInformation from "../components/pageInformation/DevOpsInformation";

const DevOpsPage = () => {
  const [card, setCard] = useState(DevOpsInformation);
  const [target, setTarget] = useState(null);

  const buttonClicked = (e) => {
    setTarget(e);
  };

  return (
    <>
      <div className="page">
        <h1>DevOps</h1>
        <h4>
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Modi dolorem
          totam temporibus quos asperiores reiciendis fuga id quasi iusto
          corporis.
        </h4>
        <div className="card">
          {card.map((item) => {
            return (
              <div className="cards" key={item.id}>
                <div className="card-header">
                  <div className="card-icon">{item.icon}</div>
                  <h2 className="card-title">{item.title}</h2>
                </div>
                <div className="card-body">{item.para}</div>
                <div className="card-footer">
                  <Link
                    to="#"
                    onClick={() => buttonClicked(item.id)}
                    className={
                      target === item.id ? "card-button active" : "card-button"
                    }
                  >
                    {item.button}
                  </Link>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
};
export default DevOpsPage;
