import { BsBoxArrowInRight } from "react-icons/bs";
import "./UserSub.css";
const UserSubItem = () => {
  return (
    <>
      <div className="UserSubItem-Container">
        <div className="user-sub-item">
          <a href="https://www.google.com" target="_blank" rel="noreferrer">
            <i className="fas fa-user-circle user-little-icon"></i>
            <span className="user-anchor">Create a Unity ID</span>
          </a>
        </div>
        <div className="user-sub-item">
          <a href="https://www.google.com" target="_blank" rel="noreferrer">
            <BsBoxArrowInRight className="user-little-icon" />
            <span className="user-anchor">Sign in</span>
          </a>
        </div>
      </div>
    </>
  );
};

export default UserSubItem;
