import { useEffect, useRef, useState } from "react";
import UserSubItem from "../subItem/userIconItem/UserSubItem";
const UserIcon = () => {
  const [open, setOpen] = useState(false);

  let menuRef = useRef();

  const clicked = () => {
    setOpen(!open);
  };
  useEffect(() => {
    document.addEventListener("mousedown", (e) => {
      if (!menuRef.current.contains(e.target)) {
        setOpen(false);
      }
    });
  });

  return (
    <div
      ref={menuRef}
      className={`user-icon-container ${open ? "active" : null}`}
    >
      <i onClick={clicked} className="fas fa-user-circle user-icon"></i>
      <UserSubItem />
    </div>
  );
};
export default UserIcon;
