import SubNav from "../components/body/SubNav";
import CreationPage from "./CreationPage";
import DevOpsPage from "./DevOpsPage";
import BackendPage from "./BackendPage";
import MultiplayerPage from "./MultiplayerPage";
import PlayerEngPage from "./PlayerEngPage";
import GrowthPage from "./GrowthPage";
import MonetizationPage from "./MonetizationPage";
import Main from "./CardPage";

import { Switch, Route } from "react-router-dom";

const CardsPage = () => {
  return (
    <>
        <SubNav />
        {
          <div className="body">
            <div className="body-container">
              <Switch>
                <Route exact path="/">
                  <Main />
                </Route>
                <Route exact path="/creation">
                  <CreationPage />
                </Route>
                <Route exact path="/devops">
                  <DevOpsPage />
                </Route>
                <Route exact path="/backend">
                  <BackendPage />
                </Route>
                <Route exact path="/multiplayer">
                  <MultiplayerPage />
                </Route>
                <Route exact path="/playerengagement">
                  <PlayerEngPage />
                </Route>
                <Route exact path="/growth">
                  <GrowthPage />
                </Route>
                <Route exact path="/monetization">
                  <MonetizationPage />
                </Route>
              </Switch>
            </div>
          </div>
        }
    </>
  );
};
export default CardsPage;
