import { useState } from "react";
import { Link } from "react-router-dom";
import MonetizationInformation from "../components/pageInformation/MonetizationInformation";

const MonetizationPage = () => {
  const [card, setCard] = useState(MonetizationInformation);
  return (
    <>
      <div className="page">
        <h1>Monetization</h1>
        <h4>
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Modi dolorem
          totam temporibus quos asperiores reiciendis fuga id quasi iusto
          corporis.
        </h4>
        <div className="card">
          {card.map((item) => {
            return (
              <div className="cards" key={item.id}>
                <div className="card-header">
                  <div className="card-icon">{item.icon}</div>
                  <h2 className="card-title">{item.title}</h2>
                  <div className="beta">{item.beta}</div>
                </div>
                <div className="card-body">{item.para}</div>
                <div className="card-footer">
                  <Link to="#" id={item.id} className="card-button">
                    {item.button}
                  </Link>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
};
export default MonetizationPage;
