import { useState } from "react";
import { Link } from "react-router-dom";
import creationInformation from "../components/pageInformation/CreationInformation";

const CreationPage = () => {
  const [card, setCard] = useState(creationInformation);
  const [target, setTarget] = useState(1);

  const buttonClicked = (e) => {
    setTarget(e);
  };

  return (
    <>
      <div className="page">
        <h1>Creation</h1>
        <h4>
          Build 2D, 3D and VR games and apps at speed. From artist tools to
          live-ops -everything you need to bring your vision to life today
        </h4>
        <div className="card">
          {card.map((item) => {
            return (
              <div className="cards" key={item.id}>
                <div className="card-header">
                  <div className="card-icon">{item.icon}</div>
                  <h2 className="card-title">{item.title}</h2>
                </div>
                <div className="card-body">{item.para}</div>
                <div className="card-footer">
                  <Link
                    to="#"
                    onClick={()=>buttonClicked(item.id)}
                    className={
                      target === item.id ? "card-button active" : "card-button"
                    }
                  >
                    {item.button}
                  </Link>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
};
export default CreationPage;
