import { useEffect, useRef, useState } from "react";
import AppSubItem from "../subItem/AppIconItem/AppSubItem";

const AppIcon = () => {
  const [open, setOpen] = useState(false);

  let menuRef = useRef();

  const clicked = () => {
    setOpen(!open);
  };
  useEffect(() => {
    document.addEventListener("mousedown", (e) => {
      if (!menuRef.current.contains(e.target)) {
        setOpen(false);
      }
    });
  });

  return (
    <div
      id="1"
      ref={menuRef}
      className={`box-icon-container ${open && "active"}`}
    >
      <i onClick={clicked} className="fas fa-th box-icon"></i>
      <AppSubItem />
    </div>
  );
};

export default AppIcon;
